import React from 'react';
import styled from 'styled-components';
import linkedin from '../img/in.png';
import fb from '../img/fb.png';
import { Fade } from 'react-reveal';

const Container = styled.div`
    display: flex;
    width: 100%;
    height: auto;
    background: #c7ffd8; 
    margin-top: -10px;
    flex-wrap: wrap;
    justify-content: center;
`

const Contactanos = styled.div`
    padding: 10px;
    margin-bottom: 10px;
    text-align: start;
    width: 40%;
    height: 130px;
    border-right: 1px solid #B6DFC3;
`

const Texto = styled.p`
    font-size: 14px;
    font-weight: 500;
    color: gray;
    margin-top: -10px;
`

const Titulo = styled.h3`
    font-size: 16px;
    color: gray;
`

const Redes = styled.div`
    padding: 10px;
    height: 100px;
    margin-left: 2px;
    border-bottom: 1px solid #B6DFC3;
    width: 100%;
    @media (min-width: 768px) {
        width: 40%;
        height: 130px;
        border-bottom: none;
        border-right: 1px solid #B6DFC3;

    }    
`

const Image = styled.img`
    width: 30px;
    &:hover {
        
    }    
`

const Nosotros = styled.div`
    width: 100%;
    min-height: 100px;
    padding: 15px;
`

const Container2 = styled.div`
    display: flex;
    width: 46%;
    flex-direction: column;
    align-items: center;
    @media (min-width: 768px) {
        flex-direction: row;
    }
`

const Link = styled.a`
    text-decoration: none;
    color: #19456b;
    transition: color .2s;
    &:hover {
        color: #00af91;
        transition: color .2s;
    }
`

const SVG = styled.svg`
    margin-top: -5px;
    filter: drop-shadow(2px -10px 5px rgba(0, 0, 0, 0.2));

`

const Footer = () => {
    return ( 
        <>
        <SVG xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="#c7ffd8" fill-opacity="1" d="M0,288L30,245.3C60,203,120,117,180,122.7C240,128,300,224,360,261.3C420,299,480,277,540,250.7C600,224,660,192,720,202.7C780,213,840,267,900,288C960,309,1020,299,1080,245.3C1140,192,1200,96,1260,69.3C1320,43,1380,85,1410,106.7L1440,128L1440,320L1410,320C1380,320,1320,320,1260,320C1200,320,1140,320,1080,320C1020,320,960,320,900,320C840,320,780,320,720,320C660,320,600,320,540,320C480,320,420,320,360,320C300,320,240,320,180,320C120,320,60,320,30,320L0,320Z"></path>
        </SVG>
        <Container>
            <Contactanos>
                <Titulo>Contáctanos</Titulo>
                <Texto>Dirección: Moreno 970 Piso 5, Ciudad Autónoma de Buenos Aires, Argentina</Texto>
                <Texto>Teléfono: +54 11-5353-5515</Texto>
                <Texto>E-Mail: <Link href="mailto: hola@geco.com.ar">hola@geco.com.ar</Link></Texto>
            </Contactanos>
            <Container2>
                <Redes>
                    <Titulo>Nuestras Redes</Titulo>
                    <a href="https://web.facebook.com/by.geco/?_rdc=1&_rdr" target="_blank"><Image src={fb} /></a>
                    <a href="https://www.linkedin.com/company/cingo-desarrollos/" target="_blank"><Image src={linkedin} /></a>
                </Redes>
                <Nosotros>
                    <Titulo>¿Quienes Somos?</Titulo>
                    <Texto>Somos una empresa de desarrollo y consultoría de software con el objetivo de brindar soluciones tecnológicas de excelencia.</Texto>
                </Nosotros>
            </Container2>
        </Container>
        </>
    );
}
 
export default Footer;