import React from 'react';
import styled from 'styled-components';
import calendar from '../../img/calendar.svg';
import turnos from '../../img/turnos.svg';
import elearning from '../../img/elearning.svg';
import cartelera from '../../img/cartelera.svg';
import cart from '../../img/cart.svg';
import institucional from '../../img/institucional.svg';
import mobile from '../../img/mobile.svg';
import soporte from '../../img/soporte.svg';


const Container = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    margin-top: 120px;
`

const Titulo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    text-align: left;
    margin-left: 10%;
    border-bottom: solid 4px #98ded9;
    color: #161d6f;
    width: 70%;
    max-width: 500px;
`

const serviciosMasVendidos = [
    {
        nombre: "Gestión de turnos",
        descripcion: "Posibilidad de solicitud de turnos online para tus clientes. Integración con TV y llamador",
        imagen: calendar
    },
    {
        nombre: "Autogestión",
        descripcion: "Sistemas para terminales de autogestión. Integracón con llamador de turnos y turnos online.",
        imagen: turnos
    },
    {
        nombre: "Cartelería digital",
        descripcion: "Gestión centralizada para sectores y sucursales.",
        imagen: cartelera
    },
    {
        nombre: "E-Commerce",
        descripcion: "Solución para vender online tus productos.",
        imagen: cart
    },
    {
        nombre: "E-Learning",
        descripcion: "Aula virtual para ofrecer tus cursos y/o seminarios. Videos, Audios, artículos y foro online.",
        imagen: elearning
    },
    {
        nombre: "Sitios web institucionales",
        descripcion: "El sitio web de tu empresa, con toda la información que tus clientes necesitan para encontrarte.",
        imagen: institucional
    },
    {
        nombre: "Apps Mobile",
        descripcion: "Multiplataforma (iOS, Android, Windows Phone y Blackberry)",
        imagen: mobile
    },
    {
        nombre: "Soporte IT",
        descripcion: "Instalación, soporte y mantenimiento de Software y Hardware.",
        imagen: soporte
    },
]

const ContainerServicios = styled.div`
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    /* background: red; */
`

const ContainerServicio = styled.div `
    width: 90%;
    max-width: 300px;
    min-height: 100px;
    background: #F3F3F3;
    margin: 10px;
    border-radius: 20px;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .1);
    /* padding: 10px; */
`

const TituloServicio = styled.p`
    font-size: 24px;
    font-weight: 700;
    background: #6EAB00;
    color: white;
    margin-top: 0;
    padding: 7px 0;
    width: 100%;

`

const DescripcionServicio = styled.p`
    margin: 0 auto;
    margin: -10px 0 20px 0;
    padding: 0 8px;
    color: #161d6f;
    font-size: 15px;
    font-weight: 600;
    text-align: center;
`

const SVG = styled.img`
    width: 120px;
    height: 120px;
    /* opacity: 30%; */
    /* position: relative; */
    /* right: 5%; */
    /* position: relative; */
    /* background: red; */
    
`

const ContainerInfo = styled.div`
    display: flex;
    align-items: center;

`

const ServiciosMasComponent = () => {

    const listarServicios = serviciosMasVendidos.map((servicio, index) => {
        return (
            <ContainerServicio key={index}>
                <TituloServicio>{servicio.nombre}</TituloServicio>
                <SVG src={servicio.imagen} />
                <DescripcionServicio>{servicio.descripcion}</DescripcionServicio>
            </ContainerServicio>)
    })

    return ( 
        <Container>
            <Titulo>SERVICIOS MÁS VENDIDOS</Titulo>
            <ContainerServicios>
                {listarServicios}
            </ContainerServicios>
        </Container>
    );
}
 
export default ServiciosMasComponent;