import React from 'react';
import ServiciosComponent from '../components/Servicios/ServiciosComponent';
import styled from 'styled-components';
import ServiciosMasComponent from '../components/Servicios/ServiciosMasComponent';

const Container = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;
    /* background: red; */
`

const Servicios = () => {
    return ( 
        <Container>
            <ServiciosComponent />
            <ServiciosMasComponent />
        </Container>
    );
}
 
export default Servicios;