import React, { useState } from 'react';
import styled from 'styled-components';
import geco from '../img/geco.png';
import {Fade} from 'react-reveal';
import { Link } from 'react-router-dom';
import Drawer from './Drawer'; 

const Navbar = styled.header`
    width: 100%;
    position: fixed; 
    top: -1px;
    left: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 2;
    @media (max-width: 1047px) {
        justify-content: space-evenly;
    }
`

const Logo = styled.img`
    width: 200px;
    margin-top: 20px;
`

const ContainerLinks = styled.div`
    width: 500px;
    display: flex;
    justify-content: space-between;
    @media (max-width: 768px) {
        display: none;
    }
`

const ContainerMenu = styled.div`
    width: auto;
    height: auto;
    @media (min-width: 1047px){
        display: none
    }
`

const Header = () => {

    const [navbar, setNavbar] = useState(false);

    const changeBackground = () => {
        if(window.scrollY >= 80){
            setNavbar(true);
        } else {
            setNavbar(false);
        }
    }

    window.addEventListener('scroll', changeBackground)    

    return ( 
        <Navbar className={(navbar) ? "headerActive" :"header"}>
            <Fade top>
                <Logo src={geco} />
            </Fade>
            <Fade top>
                <ContainerLinks>
                    <Link className="link"  to="/geotech/">Home</Link>
                    <Link className="link"  to="/geotech/servicios">Servicios</Link>
                    <Link className="link" to="/geotech/clientes" >Clientes</Link>
                    <Link className="link" >Productos</Link>
                    <Link className="link" >Contacto</Link>
                </ContainerLinks>
            </Fade>
            <ContainerMenu>
                    <Drawer />
                {/* <Fade top>
                </Fade> */}
            </ContainerMenu>
        </Navbar>
     );
}
 
export default Header;