import React from 'react';
import styled from 'styled-components';
import calendar from '../../img/calendar.svg';
import cart from '../../img/cart.svg';
import Fade from 'react-reveal/Fade';

const Container = styled.div`
    width: 100%;
    min-height: 100vh;
`

const Titulo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    text-align: left;
    margin-left: 10%;
    border-bottom: solid 4px #98ded9;
    color: #161d6f;
    width: 70%;
    max-width: 500px;
`

const TituloServicio = styled.h2`

`

const Toolbar = styled.div`
    width: 100%;
    height: 100px;
`

const Image = styled.img`
    width: 150px;
`

const ContainerServicio = styled.div`
    margin: 20px;
    padding: 15px;
    background: #F3F3F3;
    border-radius: 20px;
    filter: drop-shadow(5px 6px 4px rgba(0, 0, 0, .2));
`

const Services = styled.div`
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    margin-top: 30px;
    flex-wrap: wrap;
`
const Texto = styled.p`
    font-size: 15px;
    font-weight: 400;
    color: #0ba288;
    width: auto;
`

const Link = styled.a`
    text-decoration: none;
    color: black;
    padding: 7px;
    border-radius: 15px;
    border: solid 1px #4EE342;
    transition: all .3s;
    color: #4EE342;
    font-weight: 600;
    margin: 10px;
    &:hover {
        background: #4EE342;
        color: white;
        transition: all .3s;
    }
`
const ContainerImage = styled.div`
    width: auto;
    height: 130px;
`



const ServiciosComponent = () => {
    return ( 
        <Container>
            <Toolbar id="services" />
            <Titulo>
                <Fade top>
                    NUESTROS SERVICIOS
                </Fade>
            </Titulo>
            <Services>
                <Fade left>
                    <ContainerServicio>
                        <ContainerImage>
                            <Image src={calendar} />
                        </ContainerImage>
                        <TituloServicio>Sistemas de turnos</TituloServicio>
                        <Texto>
                            Posibilidad de solicitud de turnos online para tus clientes.
                        </Texto>
                        <Texto>
                            Integración con terminal de autoconsulta, TV y llamador.
                        </Texto>
                        <Link href="https://www.soloturnos.com/index.htm" target="_blank">VER MÁS</Link>
                    </ContainerServicio>
                </Fade>
                <Fade right>
                    <ContainerServicio>
                        <ContainerImage>
                            <Image src={cart} />
                        </ContainerImage>
                        <TituloServicio>E-commerce</TituloServicio>
                        <Texto>
                            Incluye gestión de productos por talle y color
                        </Texto>
                        <Texto>
                            Ideal para vender y promocionar tus productos online.
                        </Texto>
                        <Link href="https://www.geco.com.ar/ecommerce.php" target="_blank">VER MÁS</Link>
                    </ContainerServicio>
                </Fade>
            </Services>
        </Container>
    );
}
 
export default ServiciosComponent;