import React from 'react';
import Wave from './WaveComponent';
import styled from 'styled-components';
import bg from '../../img/bg.svg';
import { FaArrowCircleDown } from 'react-icons/fa'
import Fade from 'react-reveal/Fade';

const Container = styled.div`
    width: 100%;
    height: 100vh;
    @media (min-width: 768px){
        height: 100vh
    }
`

const Contenido = styled.div`
    height: 85vh;
    background: #c7ffd8;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    @media (min-width: 1047px) {
        height: 60vh;
    }

`
const Bg = styled.img`
    width: 150px;
`
const Texto = styled.p`
    font-size: 24px;
    font-weight: 800;
    color: #0ba288;
    padding: 10px;
`

const ContainerArrow = styled.div`
    width: auto;
    height: auto;
    position: absolute;
    bottom: 30%;
    z-index: 1;
`

const Link = styled.a`
    text-decoration: none;
    color: black;
`

const Info = styled.p`
    font-weight: 700;
    color: #0ba288;
`

const HomeComponent = () => {
    return ( 
        <Container>
            <Contenido>
                <Fade left>
                    <Texto>INNOVACIÓN, DEDICACIÓN Y EVOLUCIÓN, CON NOSOTROS</Texto>
                </Fade>
                <Fade>
                    <Bg src={bg} right big />
                </Fade>
                <ContainerArrow>
                    <Fade bottom>
                        <Info>MÁS INFORMACIÓN</Info>
                        <Link href="#services"><FaArrowCircleDown className="arrowDown" /></Link>
                    </Fade>
                </ContainerArrow>
            </Contenido>
            <Wave />
        </Container>
    );
}
 
export default HomeComponent;