import React from 'react';
import styled from 'styled-components';
import webServices from '../../img/web-services.svg';
import mobile from '../../img/mobile.svg';
import consultoria from '../../img/consultoria.svg';
import outsourcing from '../../img/outsourcing.svg';
import it from '../../img/it.svg';

const servicios = [
    {
        nombre: "Desarrollo web",
        descripcion: "Creamos soluciones orientadas a la usabilidad, amigabilidad y su apertura en los diversos dispositivos y resoluciones existentes en el mercado (Responsive Design).",
        imagen: webServices
    },
    {
        nombre: "Mobile Apps",
        descripcion: "Desarrollamos Apps multiplataforma (iOS, Android, Windows Phone y Blackberry) para que estés en la palma de la mano de tus consumidores.",
        imagen: mobile
    },
    {
        nombre: "Consultoría",
        descripcion: "Brindamos consultoría de desarrollos tecnologicos para asesorarte y ayudarte a tomar la mejor decisión.",
        imagen: consultoria
    },
    {
        nombre: "Outsourcing",
        descripcion: "Proveemos recursos de desarrollo para expandir tu capacidad y puedas alcanzar tus objetivos.",
        imagen: outsourcing
    },
    {
        nombre: "IT",
        descripcion: "Contamos con personal altamente especializado en infraestructura, con el cual podremos ayudarte a concretar tus proyectos.",
        imagen: it
    },
]

const Container = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    margin-top: 100px;
`

const Titulo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    text-align: left;
    margin-left: 10%;
    border-bottom: solid 4px #98ded9;
    color: #161d6f;
    width: 220px;
`

const ContainerServicios = styled.div`
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    /* background: red; */
`

const ContainerServicio = styled.div `
    width: 90%;
    max-width: 400px;
    min-height: 100px;
    background: #F3F3F3;
    margin: 10px;
    border-radius: 20px;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .1);
    /* padding: 10px; */
`

const TituloServicio = styled.p`
    font-size: 24px;
    font-weight: 700;
    background: #6EAB00;
    color: white;
    margin-top: 0;
    padding: 7px 0;
    width: 100%;
`

const DescripcionServicio = styled.p`
    margin: 0 auto;
    margin: -10px 0 20px 0;
    padding: 0 8px;
    color: #161d6f;
    font-size: 15px;
    font-weight: 600;
    text-align: left;
`

const SVG = styled.img`
    width: 150px;
    height: 150px;
    opacity: 30%;
    position: relative;
    right: 5%;
    /* position: relative; */
    /* background: red; */
    
`

const ContainerInfo = styled.div`
    display: flex;
    align-items: center;

`

const ServiciosComponent = () => {

    const listarServicios = servicios.map((servicio, index) => {
        return (
            < ContainerServicio key={index}>
                <TituloServicio>{servicio.nombre}</TituloServicio>
                <ContainerInfo>
                    <DescripcionServicio>{servicio.descripcion}</DescripcionServicio>
                    <SVG src={servicio.imagen} />
                </ContainerInfo>
            </ContainerServicio>)
    })

    return ( 
        <Container>
            <Titulo> SERVICIOS</Titulo>
            <ContainerServicios>{listarServicios}</ContainerServicios>
        </Container>
    );
}
 
export default ServiciosComponent;