import React from 'react';
import styled from 'styled-components';

const Ul = styled.ul`
  list-style: none;
  display: none;
  flex-flow: row nowrap;
  li {
    padding: 25px 0;
  }
  @media (max-width: 768px) {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    background: #EEFFDA;
    /* background: rgb(11,162,64); */
    /* background: linear-gradient(180deg, rgba(11,162,64,1) 0%, rgba(11,48,75,1) 100%); */
    filter: drop-shadow(-20px 0 10px rgba(0, 0, 0, 0.3));
    /* filter: drop-shadow(-300px 0 60px rgba(0, 0, 0, 0.3)); */
    /* transition: all .2s; */
    position: fixed;
    transform: ${({ open }) => open ? 'translateX(0)' : 'translateX(100%)'};
    top: -20px;
    /* left: 0; */
    right: 0;
    height: 100vh;
    width: 70%;
    max-width: 350px;
    padding-top: 5.5rem;
    transition: transform 0.3s ease-in-out;
    li {
      /* padding: 20px 0; */
      margin-bottom: 10px;
      font-size: 20px;
      font-weight: 600;
      margin-left: -30px;
      background: #DFFFBA;
      
      border-radius: 10px;
    }
  }
`;

const A = styled.a`
    text-decoration: none;
    color: black;
    width: 80%;
    margin-bottom: 20px;
    /* width: 300px; */
`

const RightNav = ({ open }) => {
  return (
    <Ul open={open}>
      <A href="/geotech/"><li>Home</li></A>
      <A href="/geotech/servicios/"><li>Servicios</li></A>
      <A href="/geotech/clientes"><li>Clientes</li></A>
      <A href="#"><li>Productos</li></A>
      <A href="#"><li>Contacto</li></A>
    </Ul>
  )
}

export default RightNav